import React from 'react';
import Slider from 'react-slick';
import './Slider.css'; // Импортируем стили для слайдера

const ImageSlider = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
  };

  return (
    <div className="slider-container">
      <Slider {...settings}>
        <div>
          <img src={`${process.env.PUBLIC_URL}/images/baner1.jpg`} alt="Banner 1" />
        </div>
        <div>
          <img src={`${process.env.PUBLIC_URL}/images/baner2.png`} alt="Banner 2" />
        </div>
        <div>
          <img src={`${process.env.PUBLIC_URL}/images/baner3.webp`} alt="Banner 3" />
        </div>
        <div>
          <img src={`${process.env.PUBLIC_URL}/images/baner4.jpg`} alt="Banner 4" />
        </div>
        <div>
          <img src={`${process.env.PUBLIC_URL}/images/baner5.webp`} alt="Banner 5" />
        </div>
        <div>
          <img src={`${process.env.PUBLIC_URL}/images/baner6.png`} alt="Banner 6" />
        </div>
      </Slider>
    </div>
  );
};

export default ImageSlider;
