import React from 'react';
import ImageSlider from './Slider';
import styles from './Home.module.css';

const Home = () => {
  return (
    <div className={styles.home}>
      <ImageSlider />
      <h1 className={styles.title}>Добро пожаловать на Последний Остров Выживания</h1>
      <p className={styles.description}>Исследуйте игру и выживите на последнем острове!</p>
    </div>
  );
};

export default Home;

